.not_found {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 350px);
}

.noAdding {
    color: red;
    font-weight: bold;
}

.edit_btn {
    margin: 5px 0 0 5%;
    position: absolute;
    display: flex;
    justify-content: space-between;
    background-color: white;
    border: none;
}

.edit_btn img {
    width: 16px;
    height: 16px;
    cursor: pointer;
}

.save_btn {
    background-color: white;
    border: none;
}

.save_btn img {
    width: 16px;
    height: 16px;
    cursor: pointer;
}

.saveNP_btn {
    margin: 5px 0 0 7%;
    position: absolute;
    display: flex;
    justify-content: space-between;
    background-color: white;
    border: none;
}

.saveNP_btn img {
    width: 16px;
    height: 16px;
    cursor: pointer;
}

.saveNP_btn:hover img {
    width: 20px;
    height: 20px;
}

.edit_btn:hover img {
    width: 20px;
    height: 20px;
}

.adding {
    color: rgb( 33, 126, 74 );
    font-weight: bold;
}

.b {
    color: #ff0000;
    font-weight: bold;
}

.c {
    font-size: 12px;
    font-weight: bold;
    color: rgb( 33, 126, 74 );
}

.d {
    font-size: 16px;
    font-weight: bold;
    color: rgb( 33, 126, 74 );
}