.login_page {
    width: 360px;
    padding: 8% 0 0;
    margin: auto;
}

.form {
    position: relative;
    z-index: 1;
    background: #FFFFFF;
    max-width: 360px;
    margin: 0 auto 100px;
    padding: 45px;
    text-align: center;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

.form input {
    font-family: "Roboto", sans-serif;
    outline: 0;
    background: #f2f2f2;
    width: 100%;
    border: 0;
    margin: 15px 0 2px;
    padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
}

.form button {
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    outline: 0;
    background: #5271ff;
    width: 100%;
    border: 0;
    padding: 15px;
    color: #FFFFFF;
    font-size: 14px;
    -webkit-transition: all 0.3 ease;
    transition: all 0.3 ease;
    cursor: pointer;
}

.form button:hover, .form button:active, .form button:focus {
    background: #002beb;
}

.blueAuth {
    font-weight: bold;
    font-size: 25px;
    color: #fff;
    text-align: center;
    margin-top: 0;
    text-shadow:
    0 0 4px #002beb,
    0 0 10px #002beb,
    0 0 18px #5271ff,
    0 0 34px #5271ff;
}

.errorAuth {
    text-align: center;
    font-size: 25px;
    font-weight: bold;
    margin-top: 0;
    /*color: #db1126;*/
    animation: flicker 1.5s infinite alternate;
    color: #fff;
}

/* Flickering animation */
@keyframes flicker {
    /*0%, 18%, 22%, 25%, 53%, 57%, 100% {*/
    100% {
        text-shadow: 0 0 4px #ff0000,
        0 0 10px #ff0000,
            /*0 0 19px #ff0000,*/ 0 0 18px #ee204d,
        0 0 24px #ee204d,
        0 0 40px #ee204d;
        /*0 0 90px #ee204d;*/
        /*0 0 100px #ee204d;*/
        /*0 0 150px #ee204d;*/
    }

    0% {
        text-shadow: 0 0 2px #ff0000,
        0 0 6px #ff0000,
        0 0 10px #ee204d,
        0 0 15px #ee204d,
        0 0 28px #ee204d;
    }
}


