.filelist {
    margin: 20px 0;
}

.filelist_header {
    display: grid;
    grid-template-columns: 1fr 4fr repeat(4, 1fr);
}

.filelist_name {
    grid-column-start: 2;
}

.filelist_date {
    grid-column-start: 5;
    justify-self: center;
}

.filelist_size {
    grid-column-start: 6;
    justify-self: center;
}

.fileplate {
    display: flex;
    flex-wrap: wrap;
    margin: 20px 0;
}

.file-enter-active {
    animation: file_enter 500ms forwards;
}
.file {
    animation: file_enter 500ms forwards;
}

@keyframes file_enter {
    0% {
        opacity: 0;
        transform: translateY(-50%);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}